@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Work+Sans&display=swap);
body {
  background-color: #fff;
}
.navbar {
  font-family: "Poppins", sans-serif;
  font-size: 15px;
}

.single_footer_part .list-unstyled li a {
  color: #7f7f7f;
  margin-bottom: 10px;
  display: inline-block;
  text-decoration: none;
}
.single_footer_part .list-unstyled li a:hover {
  color: #555555;
}

.form-control {
  background-color: #f0f0f0 !important;
}

.hero-text-div {
  margin-left: 10px;
  margin-right: 10px;
}

.review-form-control {
  background-color: #fff !important;
  font-weight: normal !important;
}

.footer_part .single_footer_part .mail_part {
  position: relative;
}
.footer_part .single_footer_part .mail_part input {
  padding: 20px 15px;
  font-size: 13px;
  border-radius: 5px;
  border: 0 solid transparent;
  margin-top: 20px;
  width: 80%;
  background-color: #eaeaea;
  outline: none;
}
::-webkit-input-placeholder {
  /* Edge */
  font-weight: 500;
  font-size: 15px;
}

:-ms-input-placeholder {
  /* Internet Explorer */
  font-weight: 500;
  font-size: 15px;
}

::placeholder {
  font-weight: 500;
  font-size: 15px;
}

.scroll-to-top {
  font-size: 1.3rem;
  line-height: 50px;
  position: fixed;
  z-index: 999;
  right: 25px;
  bottom: 25px;
  width: 50px;
  height: 50px;
  text-align: center;
  color: #ffffff;
  border-radius: 50%;
  background: #333333;
}
div.disabled {
  pointer-events: none;
  opacity: 0.55;
}
span.cart-item-name {
  font-size: 1rem;
  font-weight: 500;
  color: var(--muted);
}
.footer_part .single_footer_part .mail_part .email_icon {
  position: absolute;
  right: 0;
  top: 30px;
  background-color: #ff3368;
  text-align: center;
  color: #fff;
  border-radius: 5px;
  line-height: 10px;
  border: 2px solid #ff3368;
}
.button-contactForm {
  color: #fff;
  border-color: #edeff2;
  padding: 12px 25px;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: "Work Sans", sans-serif;
  font-weight: 400;
  color: #046c3c;
}
.mb-70 {
  margin-bottom: 70px;
}
.mb-50 {
  margin-bottom: 50px;
}
a:hover {
  text-decoration: none;
}
.discounted-price {
  font-family: "Work Sans", sans-serif;
  font-size: 1.2rem;
  font-weight: 600;
  line-height: 1;
  color: #333333;
}
.main-price.discounted {
  font-family: "Work Sans", sans-serif;
  font-size: 12px;
  font-weight: 600;
  line-height: 1;
  text-decoration: line-through;
  color: #aaaaaa;
}
.card {
  position: relative;
}
.card img {
  float: left;
  width: 300px;
  height: 300px;
  max-width: 100%;
  object-fit: cover;
  border: 1px dotted #7f7f7f;
}

.product-img {
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
  object-fit: contain;
  z-index: 99;
  opacity: 1;
}

.hotdeal {
  background: rgb(245, 232, 229);
}

.btn:disabled {
  cursor: no-drop;
}

img.hotdeal-bg-img {
  position: absolute;
  left: 50%;
  top: 45%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  height: 80%;
  z-index: 0;
}

img.hotdeal-img {
  z-index: 1;
  width: 80%;
}
.navbar-nav.basic-navbar-nav > a.nav-link {
  color: #046c3c !important;
}
.navbar-nav.basic-navbar-nav > a.nav-link.active {
  border-bottom: 3px solid #046c3c;
}

.hotdeal-img-div {
  transition: 1s;
}

input[type="checkbox"] {
  /* Double-sized Checkboxes */
  transform: scale(1.4); /* IE */
  -moz-transform: scale(1.4); /* FF */
  -webkit-transform: scale(1.4); /* Safari and Chrome */
  -o-transform: scale(1.4); /* Opera */
  padding: 10px;
  cursor: pointer;
}

@media screen and (min-width: 767px) and (max-width: 992px) {
  img.hotdeal-img.w-100 {
    width: 70% !important;
  }
  img.hotdeal-bg-img {
    height: 80%;
  }
  .footer_part .single_footer_part {
    margin-bottom: 20px;
  }
  .hero-text-div {
    margin-left: 70px;
    margin-right: 70px;
  }
}
@media screen and (max-width: 767px) {
  img.hotdeal-img {
    width: 80% !important;
  }
  img.hotdeal-bg-img {
    height: 90%;
  }
}
@media screen and (min-width: 992px) {
  .hero-text-div {
    margin-left: 120px;
    margin-right: 120px;
  }
}

@media screen and (max-width: 700px) {
  .mb-md-50 {
    margin-bottom: 50px;
  }
  .mb-md-30 {
    margin-bottom: 30px;
  }
}

@media screen and (max-width: 576px) {
  .card img {
    width: 100%;
    height: 100%;
  }
}

.zoom {
  -webkit-animation: scale 5s linear infinite;
          animation: scale 5s linear infinite;
}

@-webkit-keyframes scale {
  50% {
    -webkit-transform: scale(1.05);
    transform: scale(1.05);
  }
}

@keyframes scale {
  50% {
    -webkit-transform: scale(1.05);
    transform: scale(1.05);
  }
}

.dropdown-menu {
  color: white;
  background-color: #ff3368;
}

.dropdown-item.active,
.dropdown-item:active {
  color: #fff;
  text-decoration: none;
  background-color: #ff3368;
}

dropdown-item:focus,
.dropdown-item:hover {
  color: #fff;
  text-decoration: none;
  background-color: #ff3368;
}
a.text-light:focus,
a.text-light:hover {
  color: #fff !important;
}

.carousel-indicators li {
  background-color: black;
}
.carousel-control-next-icon {
  display: none;
}
.carousel-control-prev-icon {
  display: none;
}
a.carousel-control-prev {
  display: none;
}
a.carousel-control-next {
  display: none;
}
.carousel-item img {
  object-fit: cover;
  width: 100%;
  min-height: 572px;
  background-repeat: no-repeat;
  background: rgb(245, 232, 229);
  background-size: cover;
  background-position: center center;
}
.section-title__label-style2 p {
  font-weight: 500;
}

.section-title__label p {
  font-size: 14px;
  font-weight: 600;
  line-height: 24px;
  margin-bottom: 0;
  letter-spacing: 1px;
  color: #333333;
}
.mb-75 {
  margin-bottom: 50px;
}
.section-title__label p span:before {
  display: inline-block;
  width: 50px;
  height: 1px;
  margin: 0 15px 5px 15px;
  content: "";
  background-color: #cccccc;
}

.breadcumb-div {
  background-color: rgb(245, 232, 229);
}

.breadcumb-text {
  padding-left: 5rem;
}
.product_count {
  border: 1px solid #ddd;
  border-radius: 50px;
  display: flex;
  align-items: center;
  padding: 5px 15px 8px 15px;
}
.product_count > span {
  cursor: pointer;
}
.product_count > input {
  color: #555555;
  font-size: 1.5rem;
  font-weight: 600;
}
i.fas.fa-minus {
  color: red;
}
i.fas.fa-plus {
  color: var(--success);
}
.disable-select {
  user-select: none; /* supported by Chrome and Opera */
  -webkit-user-select: none; /* Safari */
  -khtml-user-select: none; /* Konqueror HTML */
  -moz-user-select: none; /* Firefox */
  -ms-user-select: none; /* Internet Explorer/Edge */
}

a.text-muted:hover {
  text-decoration: none;
}

.product_count input {
  border: 0 solid transparent;
  text-align: center;
}
.product_count input:focus-visible {
  outline: 0 !important;
}
.nav-pills .nav-item .nav-link {
  padding: 0;
  margin: 0.5rem;
}

/**
 * Undo the above focused button styles when the element received focus
 * via mouse click or touch, but not keyboard navigation.
 */
*:focus:not(:focus-visible) {
  outline: 0 !important;
  box-shadow: none !important;
}

.product_count .input-number {
  display: inline-block;
  width: 60px;
}

@media screen and (max-width: 500px) {
  img#breadcumb-image {
    display: none;
  }
  .nav-pills .nav-item .nav-link img {
    width: 80px;
  }
}
@media screen and (max-width: 400px) {
  .nav-pills .nav-item .nav-link img {
    width: 70px;
  }
}
@media screen and (max-width: 767px) {
  .preview-img-holder {
    display: flex;
  }
}
.nav-pills .nav-link.active,
.nav-pills .show > .nav-link {
  background-color: transparent;
  border: 1px solid var(--success);
}

.wave {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 60%;
  height: 100%;
  z-index: 0;
  overflow: hidden;
}

.img {
  display: flex;
  justify-content: flex-end;
  align-items: center;
}

.login-form {
  width: 90%;
  overflow: hidden;
}

.login-content img {
  height: 90px;
}

.login-content h2 {
  margin: 15px 0;
  color: #333;
  text-transform: uppercase;
  font-size: 2.2rem;
}

.login-content .input-div {
  position: relative;
  display: grid;
  grid-template-columns: 7% 93%;
  margin: 25px 0;
  padding: 5px 0;
  border-bottom: 2px solid #d9d9d9;
}

.login-content .input-div.one {
  margin-top: 0;
}

.i {
  color: #6c9163;
  display: flex;
  justify-content: center;
  align-items: center;
}

.i i {
  transition: 0.3s;
}

.input-div > div {
  position: relative;
  height: 45px;
}

a.logindiv-links {
  margin-top: 0.5rem;
  margin-bottom: 0.5rem;
  display: block;
  text-align: right;
  text-decoration: none;
  color: #999;
  font-size: 0.9rem;
  transition: 0.3s;
}

a.logindiv-links:hover {
  color: #38d39f;
}

.login-btn {
  display: block;
  width: 100%;
  height: 50px;
  border-radius: 25px;
  outline: none;
  border: 3px #6c9163 solid;
  background-image: linear-gradient(to right, #32be8f, #38d39f, #32be8f);
  background-size: 200%;
  font-size: 1.2rem;
  color: #fff;
  font-family: "Poppins", sans-serif;
  text-transform: uppercase;
  margin: 1rem 0;
  cursor: pointer;
  transition: 0.5s;
  max-width: 220px;
  margin: auto;
}
.login-btn:hover {
  background-position: right;
}

.input-div:before,
.input-div:after {
  content: "";
  position: absolute;
  bottom: -2px;
  width: 0%;
  height: 2px;
  background-color: #38d39f;
  transition: 0.4s;
}

.input-div:before {
  right: 50%;
}

.input-div:after {
  left: 50%;
}

.input-div.focus:before,
.input-div.focus:after {
  width: 50%;
}

.input-div.focus > div > h5 {
  top: -5px;
  font-size: 15px;
}

.input-div.focus > .i > i {
  color: #38d39f;
}

.input-div.pass {
  margin-bottom: 4px;
}

.input-div > div > input:-internal-autofill-selected {
  -webkit-appearance: menulist-button;
          appearance: menulist-button;
  background-color: rgb(232, 240, 254) !important;
  background-image: none !important;
  color: -internal-light-dark(black, white) !important;
  font: 400 1rem "Poppins";
}

.input-div > div > input {
  position: absolute;
  left: 0;
  top: 0;
  height: 100%;
  width: 90%;
  border: none;
  border-bottom: none;
  outline: none;
  background: none;
  padding: 0.5rem 0.7rem 0.2rem;
  margin: auto 10px;
  color: #555;
  font: 400 1rem "Poppins";
}

